(function() {
  'use strict';

  angular
    .module('mvpizerAdmin')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('mvpizerAdmin')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('mvpizerAdmin')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function () {
    'use strict';

    angular
        .module('mvpizerAdmin')
        .controller('ProjectController', ProjectController);

    /** @ngInject */
    function ProjectController($timeout, $scope, $stateParams, $firebaseObject, $filter) {
        var projectName = $stateParams.projectName;
        console.log(projectName);

        $scope.config = {};

        $scope.showTheme = function () {
            var selected = $filter('filter')($scope.themes, {value: $scope.config.theme});
            return ($scope.config.theme && selected.length) ? selected[0].text : 'Not set';
        };

        $scope.themes = [
            {value: 'blue', text: 'blue'},
            {value: 'red', text: 'red'},
            {value: 'orange', text: 'orange'},
            {value: 'green', text: 'green'}
        ];

        $scope.newAbout = function () {
            var aboutSection = {
                "type": "about",
                "menu_item": "",
                "title": "",
                "body": "",
                "button": {
                    text: "", link: ""
                }
            }
            $scope.config.sections.push(aboutSection);
        };

        var projectRef = new Firebase("https://mvpizer.firebaseio.com/").child(projectName);

        var syncObject = $firebaseObject(projectRef);

        syncObject.$loaded().then(function () {
                syncObject.$bindTo($scope, "config");
                $scope.config = syncObject;
                console.log($scope.config);

                if (!$scope.config.title) {
                    //First load...
                    console.log("first load...")
                    $scope.config.title = "Insert title";
                    $scope.config.sections = [];


                    $scope.config.sections.push({"type": "about", "menu_item": "First section"});
                    $scope.config.sections.push({
                        "type": "services",
                        "menu_item": "Services",
                        "title" : "Services",
                        "services": [
                            {"title": "First"}, {"title": "Second"}, {"title": "Third"}, {"title": "Forth"}
                        ]
                    });
                    $scope.config.sections.push({"type": "aside", "title" : "aside"});
                    $scope.config.sections.push({"type": "contact", "title":"contact"});
                }
            }
        ).
            catch(function (error) {
                console.log(error);
            });

    }
})
();

(function() {
  'use strict';

  angular
    .module('mvpizerAdmin')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1446577783135;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

